import React from 'react';
import { View, Text, StyleSheet, Platform, StatusBar, Alert, Button, FlatList, ScrollView, Image, TouchableHighlight, TouchableOpacity } from 'react-native';
import Fire from './Fire';
import RenderItem from "./ListItem";
import AddModal from "./AddModal";

export default class CodeOrQR extends React.Component {

    state = {
        messages: null,
        uid: '',
    }

    constructor(props) {
        super(props);
    }

    componentDidMount() {

        Fire.auth.onAuthStateChanged(user => {
            if (!user)
                this.props.navigation.navigate("Login");
        });

        this.setState({uid: Fire.uid});

        Fire.userRoomDb.once('value', (data) => {
            let dataSource = data.toJSON();
            let json = [];
            for(let i in dataSource)
                json.push(dataSource[i]);

            this.setState({messages: json});
        });
    }

    createRoom = (room) => {
        Fire.send([
            {
                name: room.name,
                roomCode: room.roomCode,
                uid: this.state.uid,
                accessLevel : room.accessLevel
            }
        ]);
    }

    onPressLogout = () => {
        Fire.auth.signOut();
        this.props.navigation.navigate("Login");
    }

    onAddPress = () => {
        this.refs.addmodal.showAddModal();
    }

    onPressScan = () => {
        this.props.navigation.navigate("BarcodeScanner");
    }

    printMessages = () => {
        alert("Join");
    }

    onPressBack = () => {
        this.props.navigation.navigate("RoomList");
    }

    render() {
        return (
            <View style={styles.container}>
                <View style={styles.headerRow}>
                    <View style={styles.left}>
                        <View style={{ flexDirection: 'row', alignItems: 'center' }}>
                            <TouchableOpacity style={{ marginRight: 10 }} onPress={this.onPressBack}>
                                <Image style={styles.backIcon} source={require('../assets/backIcon.png')}/>
                            </TouchableOpacity>
                            <Text style={styles.backButtonText}>Joint To Room</Text>
                        </View>
                    </View>
                    <View style={styles.right}/>
                </View>
                <View style={styles.buttonCon}>
                    <TouchableOpacity onPress={this.printMessages}>
                        <View style={styles.button}>
                            <Text style={styles.buttonText}>Join To Room</Text>
                        </View>
                    </TouchableOpacity>
                    <TouchableOpacity onPress={this.onPressScan}>
                        <View style={styles.button}>
                            <Text style={styles.buttonText}>Scan QR Code</Text>
                        </View>
                    </TouchableOpacity>
                </View>
            </View>
        );
    }
}

const styles = StyleSheet.create({
    container: {
        width: '100%',
        paddingTop: Platform.OS === "android" ? StatusBar.currentHeight : 0
    },
    container2: {
        width: '100%'
    },
    scroll: {
        height: '83%'
    },
    addIcon: {
        height: 40,
        width: 40,
    },
    headerRow: {
        flexDirection: "row",
        width: '100%',
        justifyContent: 'center',
        paddingBottom: 10,
        paddingTop: 10,
        backgroundColor: '#8ecfe8'
    },
    right: {
        justifyContent: 'center',
        width: '50%',
        alignItems: 'flex-end',
        paddingRight: 10
    },
    left: {
        justifyContent: 'center',
        width: '50%',
        alignItems: 'flex-start',
        paddingLeft: 10
    },
    button: {
        padding: 8,
        marginTop: 3,
        marginBottom: 3,
        height: 40,
        width: 150,
        borderRadius: 6,
        backgroundColor: '#8ecfe8',
        alignItems: 'center'
    },
    buttonCon: {
        width: 150,
        flexDirection: "row"
    },
    buttonText: {
        color: 'white',
    },
    TextContainer: {
        height: 50,
        width: '98%',
        borderWidth: 1,
        borderRadius: 10,
        marginTop: 1,
        marginBottom: 1,
        marginLeft: 3,
        marginRight: 4,
        justifyContent: 'center',
        paddingLeft: 20,
        alignItems: 'flex-start'
    },
    text: {
        fontFamily: 'sans-serif',
        fontWeight: 'bold',
        textShadowRadius: 5
    },
    textCont: {
        alignItems: 'flex-start'
    },
    backButtonText: {
        color: 'white',
    }
});
