import React from 'react';
import {
    Platform,
    StyleSheet,
    Text,
    View,
    Button,
    TextInput,
    StatusBar,
    KeyboardAvoidingView,
    SafeAreaView, TouchableOpacity, Image
} from "react-native";
import ChatFire from './ChatFire';
import { GiftedChat } from "react-native-gifted-chat";
import Fire from "./Fire";

export default class Chat extends React.Component {

    state = {
        messages: []
    }

    get user() {
        return {
            _id: ChatFire.uid,
            name: this.props.navigation.state.params.email,
            roomCode: this.roomCode
        }
    }

    get roomCode() {
        return (this.props.navigation.state.params.roomCode.data === undefined ?
            this.props.navigation.state.params.roomCode :
            this.props.navigation.state.params.roomCode.data);
    }

    componentDidMount() {
        let code = this.roomCode;

        ChatFire.get(message => {
            this.setState(previous => ({
                messages: GiftedChat.append(previous.messages, message)
            }));
        }, code);
    }

    componentWillUnmount() {
        ChatFire.off(this.roomCode);
    }

    onPressBack = () => {
        this.props.navigation.navigate("Main", {roomCode: this.props.navigation.state.params.roomCode, roomName: this.props.navigation.state.params.roomName, email: Fire.email});
    }

    render() {
        const chat = <GiftedChat messages={this.state.messages} onSend={ChatFire.send} user={this.user}/>;

        if(Platform.OS === 'android'){
            return (
                <View style={{ backgroundColor: "#8ecfe8", paddingTop: Platform.OS === 'android' ? StatusBar.currentHeight : 0 }}>
                    <View style={styles.headerRow}>
                        <View style={styles.left}>
                            <View style={{ flexDirection: 'row', alignItems: 'center' }}>
                                <TouchableOpacity style={{ marginRight: 10 }} onPress={this.onPressBack}>
                                    <Image style={styles.backIcon} source={require('../assets/backIcon.png')}/>
                                </TouchableOpacity>
                                <Text style={styles.backButtonText}>{this.props.navigation.state.params === undefined ? "No room" : this.props.navigation.state.params.roomName} Chat</Text>
                            </View>
                        </View>
                        <View style={styles.right}/>
                    </View>
                    <KeyboardAvoidingView style={{ height: "93%" }} behavior="height" keyboardVerticalOffset={30} enabled>
                        {chat}
                    </KeyboardAvoidingView>
                </View>

            );
        }

        return <SafeAreaView style={{ flex: 1 }}>{chat}</SafeAreaView>
    }
}

