import {Text, View, StyleSheet, Button, TouchableOpacity, Image, Platform, StatusBar} from 'react-native';
import { BarCodeScanner } from 'expo-barcode-scanner';
import * as Permissions from 'expo-permissions';
import * as React from 'react';
import Fire from './Fire';
import Menu, {MenuDivider, MenuItem} from "react-native-material-menu";

export default class BarcodeScanner extends React.Component {
    state = {
        hasCameraPermission: null,
        scanned: false,
        messages: null,
        userId: '',
    };

    async componentDidMount() {
        await this.getPermissionsAsync();
        await Fire.userRoomDb.once('value', (data) => {
            let dataSource = data.toJSON();
            let json = [];
            for(let i in dataSource)
                json.push(dataSource[i]);

            this.setState({messages: json});
            this.setState({userId: Fire.uid});
        });
    }

    getPermissionsAsync = async () => {
        const { status } = await Permissions.askAsync(Permissions.CAMERA);
        this.setState({ hasCameraPermission: status === 'granted' });
    }

    getRoomNameByCode = (roomCode) => {
        for(let i in this.state.messages){
            if (this.state.messages[i].roomCode === roomCode){
                return this.state.messages[i].name;
            }
        }
    }

    onBackPress = () => {
        this.props.navigation.navigate("RoomList");
    }

    render() {
        const { hasCameraPermission, scanned } = this.state;

        if (hasCameraPermission === null) {
            return <Text>Requesting for camera permission</Text>;
        }
        if (hasCameraPermission === false) {
            return <Text>No access to camera</Text>;
        }
        return (
            <View style={{ flex: 1, paddingTop: Platform.OS === 'android' ? StatusBar.currentHeight : 0, backgroundColor: '#8ecfe8'}}>
                <View style={styles.headerRow}>
                    <View style={styles.left}>
                        <View style={{ flexDirection: 'row', alignItems: 'center' }}>
                            <TouchableOpacity style={{ marginRight: 10 }} onPress={this.onBackPress}>
                                <Image style={styles.backIcon} source={require('../assets/backIcon.png')}/>
                            </TouchableOpacity>
                            <Text style={styles.backButtonText}>Scan QR Code To Join</Text>
                        </View>
                    </View>
                    <View style={styles.right}><View style={styles.addIcon}></View></View>
                </View>
                <View
                    style={{

                        height: '100%',
                        flexDirection: 'column',
                        justifyContent: 'flex-end',
                    }}>
                    <BarCodeScanner
                        onBarCodeScanned={scanned ? undefined : this.handleBarCodeScanned}
                        style={[StyleSheet.absoluteFillObject, { width: '100%' }]}
                    />

                    {scanned && (
                        <Button
                            title={'Tap to Scan Again'}
                            onPress={() => this.setState({ scanned: false })}
                        />
                    )}
                </View>
            </View>
        );
    }

    handleBarCodeScanned = ({ type, data }) => {
        this.setState({ scanned: true });
        //alert(`Bar code with type ${type} and data ${data} has been scanned!`);
        Fire.send([
            {
                name: this.getRoomNameByCode(data),
                roomCode: data,
                uid: this.state.userId,
                accessLevel : "Editor"
            }
        ]);
        this.props.navigation.navigate("Main", { roomCode: {data}, roomName: this.getRoomNameByCode(data), uid: this.state.userId});
    };
}

const styles = StyleSheet.create({
    container: {
        width: '100%',
        flex: 1,
        paddingTop: Platform.OS === "android" ? StatusBar.currentHeight : 0
    },
    container2: {
        width: '100%'
    },
    scroll: {
        height: '83%'
    },
    addIcon: {
        height: 40,
        width: 40,
    },
    backIcon: {
        height: 22.73,
        width: 30,
    },
    headerRow: {
        flexDirection: "row",
        width: '100%',
        justifyContent: 'center',
        paddingBottom: 10,
        paddingTop: 10,
        backgroundColor: '#8ecfe8'
    },
    right: {
        justifyContent: 'center',
        width: '50%',
        alignItems: 'flex-end',
        paddingRight: 10,
    },
    left: {
        justifyContent: 'center',
        width: '50%',
        alignItems: 'flex-start',
        paddingLeft: 10
    },
    button: {
        padding: 8,
        marginTop: 3,
        marginBottom: 3,
        height: 40,
        width: 150,
        borderRadius: 6,
        backgroundColor: '#8ecfe8',
        alignItems: 'center'
    },
    buttonCon: {
        //width: 150,
    },
    buttonText: {
        color: 'white',
    },
    backButton: {

    },
    backButtonText: {
        color: 'white',
    }
});
