import { StyleSheet, Text, View, Button, StatusBar, TouchableOpacity, Image, Platform } from "react-native";
import {TextInput} from "react-native-paper";
import React from "react";
import Fire from "./Fire";

export default class Login extends React.Component {

  state = {
      email: "",
      password: "",
      errorMessage: null
  };

  handleLogin = () => {
    const { email, password } = this.state;
    Fire.auth
      .signInWithEmailAndPassword(email, password)
      .then(() => { this.props.navigation.navigate("RoomList"); })
      .catch(error => this.setState({ errorMessage: error.message }));
  };

  render() {
      return (
          <View style={styles.androidSafeZone}>
              <View style={[styles.container60, styles.stickToBottom]}>
                  <View style={styles.imageIconContainer}>
                      <Image style={styles.imageIcon} source={require("../assets/AppIcon.png")}/>
                  </View>
                  <View style={styles.welcomeText}>
                      <Text style={styles.welcomeTextContent}>Welcome to Playlist App!</Text>
                  </View>
                  <View style={{ justifyContent: 'center', alignItems: 'center' }}>
                      {this.state.errorMessage && (
                          <Text style={{ color: "red" }}>{this.state.errorMessage}</Text>   // <-------------------------------------------
                      )}
                  </View>

              </View>
              <View style={[styles.container40, styles.centerContent]}>
                  <View>
                      <TextInput style={styles.textInput} label="Email Address" onChangeText={email => this.setState({ email })} value={this.state.email}/>
                      <TextInput style={styles.textInput} label="Password" onChangeText={password => this.setState({ password })} value={this.state.password}/>
                  </View>
                  <TouchableOpacity style={styles.appButtonContainer} onPress={this.handleLogin}>
                      <Text style={styles.appButtonText}>Login</Text>
                  </TouchableOpacity>
                  <View style={styles.centerContent2}>
                      <Text onPress={() => this.props.navigation.navigate("SignUp")} >Don't have an account? Sign Up</Text>
                  </View>
              </View>
          </View>
      );
  }
}
const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center"
  },
    container60: {
        height: '60%'
    },
    container40: {
        height: '40%'
    },
    imageIconContainer: {
        justifyContent: 'center',
        alignItems: 'center',
    },
    stickToBottom: {
        justifyContent: 'flex-end'
    },
    centerContent: {
        justifyContent: 'center'
    },
    centerContent2: {
        alignItems: 'center',
        marginTop: 10
    },
    imageIcon: {
        width: 230,
        height: 230
    },
    welcomeText: {
        justifyContent: 'center',
        alignItems: 'center',
    },
    welcomeTextContent: {
        fontSize: 20
    },
    textInput: {
        height: 45,
        paddingLeft: 10,
        marginTop: 10,
        marginRight: 15,
        marginLeft: 15,
        borderWidth : 1,
        borderColor : '#3abeff',
        borderRadius : 3,
        backgroundColor: '#BDF3FA'
    },
    androidSafeZone: {
        paddingTop: Platform.OS === "android" ? StatusBar.height : 0,
    },
    appButtonContainer: {
        elevation: 8,
        backgroundColor: "#3abeff",
        borderRadius: 10,
        paddingVertical: 10,
        paddingHorizontal: 12,
        marginLeft: 15,
        marginRight: 15,
        marginTop: 10
    },
    appButtonText: {
        fontSize: 18,
        color: "#fff",
        fontWeight: "bold",
        alignSelf: "center",
        textTransform: "uppercase"
    }
});
