import * as firebase from 'firebase';

class Fire {
    state={
        data: null,
        allMessage: []
    }

    constructor(){
        this.init()
        //this.checkAuth()
    }

    init = () => {
        if (!firebase.apps.length){
            firebase.initializeApp({
                apiKey: "AIzaSyCmX4C7tBPwZQDh8l734MBtUDKWOdEiO18",
                authDomain: "playlistapp-f6363.firebaseapp.com",
                databaseURL: "https://playlistapp-f6363-default-rtdb.firebaseio.com",
                projectId: "playlistapp-f6363",
                storageBucket: "playlistapp-f6363.appspot.com",
                messagingSenderId: "427902199737",
                appId: "1:427902199737:web:52985fe337d03caa2af868"
            });
        }
    };

    checkAuth = () => {
        firebase.auth().onAuthStateChanged(user => {
            if(!user){
                console.log("No user");

                /*firebase
                    .auth()
                    .signInWithEmailAndPassword("jatek125@gmail.com", "1234567890")
                    .then(() => { console.log("Successfully Logged In"); })
                    .catch(error => this.setState({ errorMessage: error.message }));*/
            }
        });
    };

    send = messages => {
        messages.forEach(item => {
            const message = {
                name: item.name,
                accessLevel: item.accessLevel,
                roomCode: item.roomCode,
                uid: item.uid,
            };

            this.userRoomDb.push(message);
        });
    };

    sendUpdateList = (messages, roomCode) => {

        let list = [];

        messages.forEach(item => {
            const message = {
                link: item.key,
                order: item.order,
                title: item.label,
            };
            list.push(message);
        });

        firebase.database().ref("playlist/" + roomCode).set(list).then(() => console.log('Data set.'));
    };

    parse = message => {
        const { accessLevel, roomCode, uid, name } = message.val();
        const { key: _id } = message;

        return {
            name,
            accessLevel,
            roomCode,
            uid
        };
    };

    parseList = message => {
        const { link, order, title } = message.val();
        const { key: _id } = message;

        return message;
    };

    get = (callback) => {
        this.userRoomDb.on("child_added", snapshot => callback(this.parse(snapshot)));
    };

    getList = (callback, roomCode = '') => {
        firebase.database().ref("playlist/" + roomCode).on("child_added", snapshot => callback(this.parseList(snapshot)));
        firebase.database().ref("playlist/" + roomCode).on("child_removed", snapshot => callback(this.parseList(snapshot)));
        firebase.database().ref("playlist/" + roomCode).on("child_changed", snapshot => callback(this.parseList(snapshot)));
        firebase.database().ref("playlist/" + roomCode).on("child_moved", snapshot => callback(this.parseList(snapshot)));
    };

    off() {
      this.userRoomDb.off();
    };

    get userRoomDb () {
        return firebase.database().ref("userRoom");
    }

    get rootDb () {
        return firebase.database().ref("");
    }

    get roomDb () {
        return firebase.database().ref("room");
    }

    get playlistRoomDb () {
        return firebase.database().ref("playlistRoom");
    }

    get databaseAlone () {
        return firebase.database();
    }

    get uid() {
        return (firebase.auth().currentUser || {}).uid;
    }

    get email() {
        return (firebase.auth().currentUser || {}).email;
    }

    get auth() {
        return firebase.auth();
    }
}

export default new Fire();
