import React, { Component } from 'react';
import { AppRegistry, FlatList, StyleSheet, Text, View, Image, Alert, Button,
    Platform, TouchableHighlight, TouchableOpacity, Dimensions, TextInput} from 'react-native';
import Modal from 'react-native-modalbox';
import QRCode from "react-qr-code";

var screen = Dimensions.get('window');

export default class AddModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            roomCode: '',
            roomName: ''
        };
    }

    showJoinModal = () => {
        this.refs.myModal.open();
    }

    onCloseButtonPress = () => {
        this.refs.myModal.close();
    }

    render(){
        return (
            <Modal style={styles.modalContainer} position='center' backdrop={true} ref={"myModal"} >
                <Text style={styles.text}>Join to {this.props.roomName === undefined ? "No room" : this.props.roomName}</Text>
                <Text style={styles.text}>Code: {this.props.roomCode === undefined ? "No room" : this.props.roomCode}</Text>
                <View style={styles.Qrcode}>
                    <QRCode
                        value={this.props.roomCode === undefined ? "No room" : this.props.roomCode}
                        size={250} includeMargin={true} level={'L'}/>
                </View>
            </Modal>
        );
    }
}

const styles = StyleSheet.create({
    modalContainer: {
        justifyContent: 'center',
        borderRadius: 30,
        shadowRadius: 10,
        width: 300,
        height: 350,
        borderWidth: 2,
        borderColor: '#8ecfe8',
        padding: 20,
    },
    textInput: {
        height: 40,
        borderBottomColor: 'gray',
        marginLeft: 30,
        marginRight: 30,
        marginTop: 20,
        marginBottom: 10,
        borderBottomWidth: 1
    },
    text: {
        fontSize: 16,
        fontWeight: 'bold',
        textAlign: 'center'
    },
    button: {
        padding: 8,
        marginTop: 3,
        marginBottom: 3,
        height: 40,
        width: 150,
        borderRadius: 6,
        backgroundColor: '#8ecfe8',
        alignItems: 'center'
    },
    buttonCon: {
        width: 150,
    },
    buttonText: {
        color: 'white',
    },
    Qrcode: {
        padding: 10,
        paddingTop: 20,
        alignItems: 'center'
    }
})
