import React from 'react';
import { View, Text, StyleSheet, Platform, StatusBar, Button, FlatList, ScrollView, Image, TouchableHighlight, TouchableOpacity } from 'react-native';
import Fire from './Fire';
import RenderItem from "./ListItem";
import AddModal from "./AddModal";
import { WebView } from "react-native-webview";

import QRCode from 'react-qr-code';
import JoinModal from "./JoinModal";
import JoinWithCodeModal from "./JoinWithCodeModal";

export default class PlayList extends React.Component {

    state = {
        messages: null,
        uid: '',
    }

    onPressBack = () => {
        this.props.navigation.navigate("RoomList");
    }

    onScanPress = () => {
        this.props.navigation.navigate("BarcodeScanner");
    }

    onListPress = () => {
        this.props.navigation.navigate("Main");
    }

    onJoinPress = () => {
        this.refs.joinmodal.showJoinModal();
    }

    constructor(props) {
        super(props);
    };

    render() {
        return (
            <View style={[styles.container]}>
                <View style={[styles.headerRow]}>
                    <View style={styles.left}>
                        <View style={{ flexDirection: 'row', alignItems: 'center' }}>
                            <TouchableOpacity style={{ marginRight: 10 }} onPress={this.onPressBack}>
                                <Image style={styles.backIcon} source={require('../assets/backIcon.png')}/>
                            </TouchableOpacity>
                            <Text style={styles.backButtonText}>{this.props.navigation.state.params === undefined ? "No room" : this.props.navigation.state.params.roomName}</Text>
                        </View>
                    </View>
                    <View style={styles.right}>
                        <TouchableOpacity onPress={this.onJoinPress}>
                            <Image style={styles.addIcon} source={require('../assets/addIcon.png')}/>
                        </TouchableOpacity>
                    </View>
                </View>
                <View style={styles.buttonCon}>
                    <TouchableOpacity onPress={this.onJoinPress}>
                        <View style={styles.button}>
                            <Text style={styles.buttonText}>Open Join Modal</Text>
                        </View>
                    </TouchableOpacity>
                </View>
                <View style={styles.buttonCon}>
                    <TouchableOpacity onPress={this.onScanPress}>
                        <View style={styles.button}>
                            <Text style={styles.buttonText}>Scan QR Code</Text>
                        </View>
                    </TouchableOpacity>
                </View>
                <View style={styles.buttonCon}>
                    <TouchableOpacity onPress={this.onListPress}>
                        <View style={styles.button}>
                            <Text style={styles.buttonText}>Main</Text>
                        </View>
                    </TouchableOpacity>
                </View>
                <JoinModal ref={'joinmodal'}
                           roomCode={this.props.navigation.state.params === undefined ? "No room" : this.props.navigation.state.params.roomCode}
                           roomName={this.props.navigation.state.params === undefined ? "No room" : this.props.navigation.state.params.roomName}/>
            </View>
        );
    }
}

const styles = StyleSheet.create({
    container: {
        backgroundColor: '#8ecfe8',
        width: '100%',
        flex: 1,
        marginTop: Platform.OS === "android" ? StatusBar.currentHeight : 0
    },
    container2: {
        width: '100%'
    },
    scroll: {
        height: '83%'
    },
    addIcon: {
        height: 40,
        width: 40,
    },
    backIcon: {
        height: 22.73,
        width: 30,
    },
    headerRow: {
        flexDirection: "row",
        width: '100%',
        justifyContent: 'center',
        paddingBottom: 10,
        paddingTop: 10,
        backgroundColor: '#8ecfe8'
    },
    right: {
        justifyContent: 'center',
        width: '50%',
        alignItems: 'flex-end',
        paddingRight: 10,
    },
    left: {
        justifyContent: 'center',
        width: '50%',
        alignItems: 'flex-start',
        paddingLeft: 10
    },
    button: {
        padding: 8,
        marginTop: 3,
        marginBottom: 3,
        height: 40,
        width: 150,
        borderRadius: 6,
        backgroundColor: '#8ecfe8',
        alignItems: 'center'
    },
    buttonCon: {
        //width: 150,
    },
    buttonText: {
        color: 'white',
    },
    backButton: {

    },
    backButtonText: {
        color: 'white',
    }
});
