import {View, TouchableOpacity, Text, StyleSheet, Button, Alert, StatusBar, Platform, Image} from 'react-native';
import Menu, { MenuItem, MenuDivider } from 'react-native-material-menu';
import DraggableFlatList, { RenderItemParams, } from 'react-native-draggable-flatlist';
import Animated from 'react-native-reanimated';
import React, { Component } from 'react';
import Fire from './Fire';
import MenuModal from "./MenuModal";
import JoinModal from "./JoinModal";
import AddMusicModal from "./AddMusicModal";
import { Audio } from 'expo-av';
import ytdl from "react-native-ytdl"

const { block, set, onChange, Clock, Value, startClock, stopClock, clockRunning, cond, spring, call, } = Animated;

type Item = {
    key: string;
    label: string;
    height: number;
    width: number;
    playing: boolean;
    order: number;
};

const RowItem = ({ item,  drag,  isActive,  scale }: RenderItemParams<Item> & { scale: Animated.Node<number> | number }) => {
    return (
        <Animated.View
            style={{
                flex: 1,
                paddingHorizontal: 10,
                alignItems: 'center' as const,
                flexDirection: 'row' as const,
                elevation: isActive ? 10 : 0,
                shadowRadius: isActive ? 10 : 0,
                shadowColor: isActive ? 'black' : 'transparent',
                shadowOpacity: isActive ? 0.25 : 0,
                transform: [{ scaleX: scale }, { scaleY: scale }],
                backgroundColor: item.playing ? "black" : (item.order === 9999 ? "#f25a49" : "lightgray"),
                justifyContent: 'center',
            }}>
            {item.order !== 9999 ?
                <View style={{ width: '95%', borderBottomWidth: 0.4, borderColor: "#000000" }}>
                    <TouchableOpacity style={styles.rowItem} onLongPress={drag}>
                        <Text style={styles.text}>{item.label}</Text>
                    </TouchableOpacity>
                </View> :
                <View style={styles.rowItemDelete}>
                    <Text style={styles.textDelete}>{item.label}</Text>
                </View>}
        </Animated.View>
    );
};

class Main extends Component {
    state = {
        data: [],
        height: 60 + Math.random() * 100,
        width: 40 + Math.random() * 40,
        messages: null,
        uid: '',
        plusz: {
            key: `DeleteLink`,
            label: 'Delete',
            height: 60 + Math.random() * 100,
            width: 40 + Math.random() * 40,
            order: 9999,
        },
        testData: {
            key: `TestAddLink`,
            label: 'TestAdd',
            height: 60 + Math.random() * 100,
            width: 40 + Math.random() * 40,
            order: null,
        },
        isPlaying: false,
        source: 0,
        linkList: null
    };

    onPressBack = () => {
        this.props.navigation.navigate("RoomList");
    }

    onJoinPress = () => {
        this.refs.joinModal.showJoinModal();
    }

    onAddModalPress = () => {
        this.refs.addMusicModal.showAddModal();
    }

    //-----------------------------------------------------------------------------------------

    onAddPress = (newMusic) => {
        let json = [];
        let lastOrder =  0;
        for(let i in this.state.data){
            if (this.state.data[i].order !== 9999){
                json.push(this.state.data[i]);
                lastOrder = this.state.data[i].order;
            }
        }
        let pluszJson = {
            key: newMusic.link,
            label: newMusic.title,
            height: 60 + Math.random() * 100,
            width: 40 + Math.random() * 40,
            order: null,
        };
        pluszJson.order = lastOrder + 1;
        json.push(pluszJson);

        this.setState({data: json});

        //Call database update
        let code = (this.props.navigation.state.params.roomCode.data === undefined ? this.props.navigation.state.params.roomCode : this.props.navigation.state.params.roomCode.data);
        Fire.sendUpdateList(json, code);

        json = this.addDeleteItem(json);
        this.setState({data: json});
    }

    removeDeleteItem = (music) => {
        let json = [];
        for(let i in music){
            if(music[i].order !== 9999)
                json.push(music[i]);
        }
        return json;
    }

    addDeleteItem = (music) => {
        let json = [];
        for(let i in music){
            json.push(music[i]);
        }
        json.push(this.state.plusz);
        return json;
    }

    //Kell-e trlni
    checkDeleteItem = (music) => {
        if (music[music.length - 1].order === 9999){
            return false;
        }
        else{
            return true;
        }
    }

    addMusicToList = (music) => {

        if(this.checkDeleteItem(music)){
            music.pop();
            music.pop();
        }
        else{
            music.pop();
            let oldOrder  = [];
            let changeFound = false;
            let prevOrder = 0;
            for(let i in this.state.data){
                oldOrder.push(this.state.data[i].order);
            }
            for(let i in music){
                if(music[i].order !== oldOrder[i]){
                    if(changeFound){
                        music[i].order = prevOrder + 1;
                        prevOrder += 1;
                    }
                    else{
                        changeFound = true;
                        music[i].order = prevOrder + 1;
                        prevOrder += 1;
                    }
                }
                else {
                    if(changeFound){
                        music[i].order = prevOrder + 1;
                        prevOrder += 1;
                    }
                    else{
                        prevOrder = oldOrder[i];
                    }
                }
            }
            this.setState({data: music});
            this.setState({messages: music});
        }

        //Update Database
        let code = (this.props.navigation.state.params.roomCode.data === undefined ? this.props.navigation.state.params.roomCode : this.props.navigation.state.params.roomCode.data);
        Fire.sendUpdateList(music, code);

        let saltedWithDelete = music.length > 0 ? this.addDeleteItem(music) : music;
        this.setState({data: saltedWithDelete});
        this.setState({messages: saltedWithDelete});
    }

    //-----------------------------------------------------------------------------------------

    constructor(props) {
        super(props);
        this.loadAudio = this.loadAudio.bind(this);
        this.toggleAudioPlayback = this.toggleAudioPlayback.bind(this);
    };

    async componentDidMount() {
        this.initAudio();
        let code = (this.props.navigation.state.params.roomCode.data === undefined ? this.props.navigation.state.params.roomCode : this.props.navigation.state.params.roomCode.data);

        console.log(code);
        Fire.getList(message => {
            Fire.databaseAlone.ref("playlist/" + (this.props.navigation.state.params.roomCode.data === undefined ? this.props.navigation.state.params.roomCode : this.props.navigation.state.params.roomCode.data)).once('value', (data) => {
                let dataSource = data.toJSON();
                let json = [];
                for(let i in dataSource){
                    let tempData = dataSource[i];
                    tempData.key = dataSource[i].link;
                    tempData.label = dataSource[i].title;
                    tempData.height = 60 + Math.random() * 100;
                    tempData.width = 40 + Math.random() * 40;
                    tempData.order = dataSource[i].order;
                    json.push(tempData);
                }

                json.sort(function(a, b){return a.order - b.order});

                let links = [];
                for(let i in json){
                    links.push(json[i].key);
                }
                this.setState({linkList: links});

                let saltedJson = this.addDeleteItem(json);

                this.setState({messages: saltedJson});
                this.setState({data: saltedJson});
                console.log("Updated");
            });
        }, code);
    }

    renderItem = ({ item, drag, isActive }: RenderItemParams<Item>) => {

        let playing = item;
        if (item.order === this.state.source-1){
            item.playing = true;
        }
        else{
            item.playing = false;
        }

        return (
            <RowItem
                item={item}
                drag={drag}
                isActive={isActive}
                scale={isActive ? this.animState.position : 1}
            />
        );
    };

    onPressDelete = () => {
        console.log(this.state.data);
    }

    onLogPress = () => {
        console.log(this.state.data);
    }

    onChatPress = () => {
        this.props.navigation.navigate("Chat", {roomCode: this.props.navigation.state.params.roomCode, roomName: this.props.navigation.state.params.roomName, email: Fire.email});
    }

    isActive = new Animated.Value(0);
    clock = new Clock();
    animConfig = {
        damping: 20,
        mass: 0.4,
        stiffness: 100,
        overshootClamping: false,
        restSpeedThreshold: 0.2,
        restDisplacementThreshold: 0.2,
        toValue: new Value(0),
    };
    animState = {
        finished: new Value(0),
        velocity: new Value(0),
        position: new Value(1),
        time: new Value(0),
    };

    //Menu/////////////////////////////
    _menu = null;

    setMenuRef = ref => {
        this._menu = ref;
    };

    hideMenu = () => {
        this._menu.hide();
    };

    showMenu = () => {
        this._menu.show();
    };
    //////////////////////////////////

    // Play Music //

    _onPlaybackStatusUpdate = async playbackStatus => {
        if (playbackStatus.didJustFinish)
            try {
                let link = this.generateLink(this.state.linkList[this.state.source]).then((value) => {
                    this.soundObject.unloadAsync();
                    this.soundObject.loadAsync({uri: value.toString()}).then(() => {
                        this.soundObject.playAsync();
                        this.setState({source: (this.state.source + 1)});
                    });
                });
            } catch (e) {
                console.log('ERROR Loading Audio', e);
            }
    };

    componentWillUnmount() {
        this.soundObject.stopAsync();
    }

    async loadAudio() {
        this.soundObject = new Audio.Sound();
        this.soundObject.setOnPlaybackStatusUpdate(this._onPlaybackStatusUpdate);
        try {
            let link = this.generateLink(this.state.linkList[this.state.source]).then((value) => {
                this.soundObject.loadAsync({uri: "https://cors-anywhere.herokuapp.com/" + value.toString()});
                this.setState({source: (this.state.source + 1)});
            });
        } catch (e) {
            console.log('ERROR Loading Audio', e);
        }
    }

    initAudio = () => {
        this.soundObject = new Audio.Sound();
        this.soundObject.setOnPlaybackStatusUpdate(this._onPlaybackStatusUpdate);
    }

    toggleAudioPlayback() {
        this.setState({ isPlaying: !this.state.isPlaying, }, () => {
            if(this.state.isPlaying){
                try {
                    let link = this.generateLink(this.state.linkList[this.state.source]).then((value) => {
                        this.soundObject.unloadAsync();
                        this.soundObject.loadAsync({uri: value.toString()}).then(() => {
                            this.soundObject.playAsync();
                            this.setState({source: (this.state.source + 1)});
                        });
                    });
                } catch (e) {
                    console.log('ERROR Loading Audio', e);
                }
            }
            else{
                this.soundObject.stopAsync();
            }
        });
    }

    playMusic = () => {
        try {
            let link = this.generateLink(this.state.linkList[this.state.source]).then((value) => {
                this.soundObject.unloadAsync();
                this.soundObject.loadAsync({uri: value.toString()}).then(() => {
                    this.soundObject.playAsync();
                    this.setState({source: (this.state.source + 1)});
                });
            });
        } catch (e) {
            console.log('ERROR Loading Audio', e);
        }
    }

    reStart = () => {
        this.setState({source: 0});
    }

    prevMusic = () => {
        this.soundObject.stopAsync();
        try {
            let link = this.generateLink(this.state.linkList[((this.state.source - 2) < 0 ? 0 : (this.state.source - 2))]).then((value) => {
                this.soundObject.unloadAsync();
                this.soundObject.loadAsync({uri: value.toString()}).then(() => {
                    this.soundObject.playAsync();
                    this.setState({source: (((this.state.source - 2) < 0 ? 0 : (this.state.source - 2)) + 1)});
                });
            });
        } catch (e) {
            console.log('ERROR Loading Audio', e);
        }
    }

    nextMusic = () => {
        this.soundObject.stopAsync();
        this.playMusic();
    }

    generateLink = async (videoId) => {
        let info = await ytdl.getInfo(videoId);
        let format = ytdl.chooseFormat(info.formats, {quality: '140'});
        return format.url;
    }

    ////////////////

    currentMusic = () => {
        if (this.state.source !== 0){
            return (
                <View style={{ backgroundColor: "#6bc5e8", height: 30, justifyContent: 'center', alignItems: 'center' }}>
                    <Text style={{ color: "white" }}>Playing: {this.state.data[this.state.source-1].label}</Text>
                </View>
            );
        }
        return;
    }

    render() {
        return (
            <View style={styles.container}>
                <View style={styles.headerRow}>
                    <View style={styles.left}>
                        <View style={{ flexDirection: 'row', alignItems: 'center' }}>
                            <TouchableOpacity style={{ marginRight: 10 }} onPress={this.onPressBack}>
                                <Image style={styles.backIcon} source={require('../assets/backIcon.png')}/>
                            </TouchableOpacity>
                            <Text style={styles.backButtonText}>{this.props.navigation.state.params === undefined ? "No room" : this.props.navigation.state.params.roomName}</Text>
                        </View>
                    </View>
                    <View style={styles.right}>
                        <TouchableOpacity style={{ marginRight: 10 }} onPress={this.showMenu}>
                            <Image style={styles.addIcon} source={require('../assets/addIcon.png')}/>
                        </TouchableOpacity>
                        <Menu ref={this.setMenuRef}>
                            <MenuItem onPress={() => { this.hideMenu(); this.onJoinPress(); }}>Share Room</MenuItem>
                            <MenuItem onPress={() => { this.hideMenu(); this.onAddModalPress(); }}>Add Music</MenuItem>
                            <MenuItem onPress={() => { this.hideMenu(); this.onChatPress(); }}>Chat</MenuItem>
                            <MenuItem onPress={this.hideMenu}>Logout</MenuItem>
                        </Menu>
                    </View>
                </View>
                <DraggableFlatList style={{backgroundColor: "white"}}
                    data={this.state.data}
                    renderItem={this.renderItem}
                    onDragBegin={() => this.isActive.setValue(1)}
                    onRelease={() => this.isActive.setValue(0)}
                    keyExtractor={(item, index) => `draggable-item-${item.key}`}
                    onDragEnd={({ data }) => { this.addMusicToList(data); console.log(this.state.data)}}
                />
                <Animated.Code>
                    {() =>
                        block([
                            onChange(this.isActive, [
                                set(this.animConfig.toValue, cond(this.isActive, 1.5, 1)),
                                startClock(this.clock),
                            ]),
                            cond(clockRunning(this.clock), [
                                spring(this.clock, this.animState, this.animConfig),
                                cond(this.animState.finished, [
                                    stopClock(this.clock),
                                    set(this.animState.finished, 0),
                                    set(this.animState.time, 0),
                                    set(this.animState.velocity, 0),
                                ]),
                            ]),
                        ])
                    }
                </Animated.Code>
                <JoinModal ref={'joinModal'}
                           roomCode={this.props.navigation.state.params === undefined ? "No room" : this.props.navigation.state.params.roomCode}
                           roomName={this.props.navigation.state.params === undefined ? "No room" : this.props.navigation.state.params.roomName}/>
                <AddMusicModal ref={'addMusicModal'} parentFlatList={this}/>

                {this.currentMusic()}

                <View style={{ flexDirection: "row", height: 40 }}>
                    <TouchableOpacity onPress={this.reStart} style={{ height: 40, width: "25%", backgroundColor: "#8ecfe8", justifyContent: "center", alignItems: "center" }}>
                        <Image  style={{ height: 20, width: 20 }} source={require('../assets/reset.png')}/>
                    </TouchableOpacity>
                    <TouchableOpacity onPress={this.prevMusic} style={{ height: 40, width: "25%", backgroundColor: "#8ecfe8", justifyContent: "center", alignItems: "center" }}>
                        <Image  style={{ height: 20, width: 35.5 }} source={require('../assets/prev.png')}/>
                    </TouchableOpacity>
                    <TouchableOpacity onPress={this.toggleAudioPlayback} style={{ height: 40, width: "25%", backgroundColor: "#8ecfe8", justifyContent: "center", alignItems: "center" }}>
                        <Image  style={{ height: 20, width: 17.775 }} source={require('../assets/play.png')}/>
                    </TouchableOpacity>
                    <TouchableOpacity onPress={this.nextMusic} style={{ height: 40, width: "25%", backgroundColor: "#8ecfe8", justifyContent: "center", alignItems: "center" }}>
                        <Image  style={{ height: 20, width: 35.5 }} source={require('../assets/next.png')}/>
                    </TouchableOpacity>
                </View>
            </View>
        );
    }
}

export default Main;

