import React, { Component } from 'react';
import { AppRegistry, FlatList, StyleSheet, Text, View, Image, Alert, Button,
    Platform, TouchableHighlight, TouchableOpacity, Dimensions, TextInput} from 'react-native';

import Modal from 'react-native-modalbox';
//import Button from 'react-native-button';

var screen = Dimensions.get('window');

export default class AddModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            newMusicName: '',
            newMusicLink: ''
        };
    }
    showAddModal = () => {
        this.refs.myModal.open();
    }

    onCreateButtonPress = () => {
        if (this.state.newMusicName.length === 0){
            alert("You must enter music name!");
        }
        else if(this.state.newMusicLink.length === 0){
            alert("You must provide a link to the music!");
        }
        else{

            let videoId = this.state.newMusicLink;
            let videoSliced = videoId.split('watch?v=');
            if(videoSliced[1] === undefined){
                alert("You must provide a CORRECT link to the music!");
            }
            else{
                this.props.parentFlatList.onAddPress(
                    {
                        link: videoSliced[1],
                        title: this.state.newMusicName,
                        order: ''
                    }
                );
                this.refs.myModal.close();
            }


        }
    }
    onCloseButtonPress = () => {
        this.refs.myModal.close();
    }
    render(){
        return (
            <Modal style={styles.modalContainer} position='center' backdrop={true} ref={"myModal"} onClose={() => {
                alert("Modal Closed");
            }}>
                <Text style={styles.text}>Add New Music</Text>
                <TextInput style={styles.textInput} placeholder="Enter music name" value={this.state.newMusicName} onChangeText={
                    (text) => this.setState({newMusicName: text})
                }/>
                <TextInput style={styles.textInput2} placeholder="Enter music link" value={this.state.newMusicLink} onChangeText={
                    (text) => this.setState({newMusicLink: text})
                }/>
                <View style={{ alignItems: "center" }}>
                    <View style={styles.buttonCon}>
                        <TouchableOpacity onPress={this.onCreateButtonPress}>
                            <View style={styles.button}>
                                <Text style={styles.buttonText}>Add</Text>
                            </View>
                        </TouchableOpacity>
                    </View>
                </View>
            </Modal>
        );
    }
}

const styles = StyleSheet.create({
    modalContainer: {
        justifyContent: 'center',
        borderRadius: 30,
        shadowRadius: 10,
        width: screen.width - 80,
        height: 280,
        borderWidth: 2,
        borderColor: '#8ecfe8'
    },
    textInput: {
        height: 40,
        borderBottomColor: 'gray',
        marginLeft: 30,
        marginRight: 30,
        marginTop: 20,
        marginBottom: 10,
        borderBottomWidth: 1
    },
    textInput2: {
        height: 40,
        borderBottomColor: 'gray',
        marginLeft: 30,
        marginRight: 30,
        marginBottom: 10,
        borderBottomWidth: 1
    },
    text: {
        fontSize: 16,
        fontWeight: 'bold',
        textAlign: 'center',
        marginTop: 40
    },
    button: {
        padding: 8,
        marginTop: 3,
        marginBottom: 3,
        height: 40,
        width: 150,
        borderRadius: 6,
        backgroundColor: '#8ecfe8',
        alignItems: 'center'
    },
    buttonCon: {
        width: 150,
    },
    buttonText: {
        color: 'white',
    }
})
