import React from 'react';
import { View, Text, StyleSheet, Platform, StatusBar, Button, FlatList, ScrollView, Image, TouchableHighlight, TouchableOpacity } from 'react-native';
import Fire from './Fire';
import RenderItem from "./ListItem";
import AddModal from "./AddModal";
import { createDrawerNavigator } from '@react-navigation/drawer';
import { NavigationContainer } from '@react-navigation/native';
import CodeOrQR from "./CodeOrQR";
import Menu, { MenuItem, MenuDivider } from 'react-native-material-menu';
import JoinWithCodeModal from "./JoinWithCodeModal";

const Drawer = createDrawerNavigator();

export default class RoomList extends React.Component {

    state = {
        messages: null,
        joinMessages: null,
        uid: '',
    }

    constructor(props) {
        super(props);
    }

    componentDidMount() {

        Fire.auth.onAuthStateChanged(user => {
            this.props.navigation.navigate(user ? "RoomList" : "Login");
        });

        this.setState({uid: Fire.uid});
        Fire.get(message => {
            let id = Fire.uid;
            Fire.userRoomDb.once('value', (data) => {
                let dataSource = data.toJSON();
                let json = [];
                for(let i in dataSource){
                    if (dataSource[i].uid === id){
                        json.push(dataSource[i]);
                    }
                }

                this.setState({messages: json});
            });

            Fire.userRoomDb.once('value', (data) => {
                let dataSource = data.toJSON();
                let json = [];
                for(let i in dataSource)
                    json.push(dataSource[i]);

                this.setState({joinMessages: json});
                this.setState({uid: Fire.uid});
            });
        });
    }

    //Menu/////////////////////////////
    _menu = null;

    setMenuRef = ref => {
        this._menu = ref;
    };

    hideMenu = () => {
        this._menu.hide();
    };

    showMenu = () => {
        this._menu.show();
    };
    //////////////////////////////////

    getUid = () => {
        return Fire.uid;
    }

    createRoom = (room) => {
        Fire.send([
            {
                name: room.name,
                roomCode: room.roomCode,
                uid: this.state.uid,
                accessLevel : room.accessLevel
            }
        ]);
    }

    getRoomNameByCode = (roomCode) => {
        for(let i in this.state.joinMessages){
            if (this.state.joinMessages[i].roomCode === roomCode){
                return this.state.joinMessages[i].name;
            }
        }
    }

    isUnique = (roomCode) => {
        let alreadyUsed = false;
        this.state.joinMessages.forEach(room => {
            if (String(room.roomCode).valueOf() === String(roomCode).valueOf()){
                alreadyUsed = true;
            }
        });
        if (alreadyUsed){
            return false;
        }
        else{
            return true;
        }
    }

    JoinToRoom = (roomCode) => {
        if(this.getRoomNameByCode(roomCode) !== undefined){
            Fire.send([
                {
                    name: this.getRoomNameByCode(roomCode),
                    roomCode: roomCode,
                    uid: this.state.uid,
                    accessLevel : "Editor"
                }
            ]);
            this.props.navigation.navigate("Main", { roomCode: {roomCode}, roomName: this.getRoomNameByCode(roomCode), uid: this.state.uid});
        }
        else{
            alert("There is no room with such a code!");
        }

    }

    onCodePress = () => {
        this.refs.codemodal.showJoinModal();
    }

    printMessages = () => {
        console.log(this.isUnique("QJN0LC"));
    }

    onPressLogout = () => {
        Fire.auth.signOut();
        this.props.navigation.navigate("Login");
    }

    onPressJoin = () => {
        this.props.navigation.navigate("CodeOrQR");
    }

    onAddPress = () => {
        this.refs.addmodal.showAddModal();
    }

    onPressScan = () => {
        this.props.navigation.navigate("BarcodeScanner");
    }

    render() {
        return (
            <View style={{ backgroundColor: '#8ecfe8' }}>
                <View style={[styles.container, {backgroundColor: 'white', height: '100%'}]}>
                    <View style={styles.headerRow}>
                        <View style={styles.left}>
                            <Text style={styles.backButtonText} >Rooms</Text>
                        </View>
                        <View style={styles.right}>
                            <TouchableOpacity onPress={this.showMenu}>
                                <Image style={styles.addIcon} source={require('../assets/addIcon.png')}/>
                            </TouchableOpacity>
                            <Menu ref={this.setMenuRef}>
                                <MenuItem onPress={() => { this.hideMenu(); this.onAddPress(); }}>Create Room</MenuItem>
                                <MenuItem onPress={() => { this.hideMenu(); this.onCodePress(); }}>Join Room With Code</MenuItem>
                                <MenuItem onPress={() => { this.hideMenu(); this.onPressScan(); }}>Scan QR Code</MenuItem>
                                <MenuDivider/>
                                <MenuItem onPress={() => { this.hideMenu(); this.onPressLogout(); }}>Logout</MenuItem>
                            </Menu>
                        </View>
                    </View>
                    <ScrollView style={styles.scroll}>
                        <FlatList style={styles.container2} data={this.state.messages === undefined ? [{name : "Empty List"}] : this.state.messages} keyExtractor={item => item.roomCode} renderItem={
                            ({item}) => {
                                const Item = ({ title }) => (
                                    <View style={styles.textCont}>
                                        <Text style={styles.text}>{title}</Text>
                                    </View>
                                );

                                return (
                                    <TouchableOpacity onPress={() => {this.props.navigation.navigate("Main", {roomCode: item.roomCode, roomName: item.name, email: Fire.email});}}>
                                        <View style={styles.TextContainer}>
                                            <Item title={item.name} />
                                        </View>
                                    </TouchableOpacity>
                                );
                            }}/>
                    </ScrollView>
                    <AddModal ref={'addmodal'} parentFlatList={this} />
                    <JoinWithCodeModal ref={'codemodal'} parentFlatList={this} />
                </View>
            </View>
        );
    }
}

const styles = StyleSheet.create({
    container: {
        width: '100%',
        marginTop: Platform.OS === "android" ? StatusBar.currentHeight : 0
    },
    container2: {
        width: '100%'
    },
    scroll: {
        height: '83%'
    },
    addIcon: {
        height: 40,
        width: 40,
    },
    headerRow: {
        flexDirection: "row",
        width: '100%',
        justifyContent: 'center',
        paddingBottom: 10,
        paddingTop: 10,
        backgroundColor: '#8ecfe8'
    },
    right: {
        justifyContent: 'center',
        width: '50%',
        alignItems: 'flex-end',
        paddingRight: 10
    },
    left: {
        justifyContent: 'center',
        width: '50%',
        alignItems: 'flex-start',
        paddingLeft: 10
    },
    button: {
        padding: 8,
        marginTop: 3,
        marginBottom: 3,
        height: 40,
        width: 150,
        borderRadius: 6,
        backgroundColor: '#8ecfe8',
        alignItems: 'center'
    },
    buttonCon: {
        width: 150,
        flexDirection: "row"
    },
    buttonText: {
        color: 'white',
    },
    TextContainer: {
        height: 50,
        width: '98%',
        borderWidth: 1,
        borderRadius: 10,
        marginTop: 1,
        marginBottom: 1,
        marginLeft: 3,
        marginRight: 4,
        justifyContent: 'center',
        paddingLeft: 20,
        alignItems: 'flex-start'
    },
    text: {
        fontFamily: 'sans-serif',
        fontWeight: 'bold',
        textShadowRadius: 5
    },
    textCont: {
        alignItems: 'flex-start'
    },
    backButtonText: {
        color: 'white',
    }
});
