import React, { Component } from 'react';
import { AppRegistry, FlatList, StyleSheet, Text, View, Image, Alert, Button,
         Platform, TouchableHighlight, TouchableOpacity, Dimensions, TextInput} from 'react-native';

import Modal from 'react-native-modalbox';
//import Button from 'react-native-button';

var screen = Dimensions.get('window');

export default class AddModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            newRoomName: ''
        };
    }
    showAddModal = () => {
        this.refs.myModal.open();
    }

    generateRoomCode = () => {
        let result           = '';
        let characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789';
        let charactersLength = characters.length;

        while (!this.props.parentFlatList.isUnique(result)){
            result = '';
            for ( let i = 0; i < 6; i++ ) {
                result += characters.charAt(Math.floor(Math.random() * charactersLength));
            }
            console.log(result);
        }

        return result;
    }

    onCreateButtonPress = () => {
        if (this.state.newRoomName.length === 0){
            alert("You must enter room name");
            return;
        }
        else{
            const newRoom = {
                name: this.state.newRoomName,
                roomCode: this.generateRoomCode(),
                uid: this.props.parentFlatList.state.uid,
                accessLevel : "Owner"
            };
            //this.props.parentFlatList.refreshFlatList(newRoom);
            this.props.parentFlatList.createRoom(newRoom);
            this.refs.myModal.close();
        }

    }
    onCloseButtonPress = () => {
        this.refs.myModal.close();
    }
    render(){
        return (
            <Modal style={styles.modalContainer} position='center' backdrop={true} ref={"myModal"} onClose={() => {
                alert("Modal Closed");
            }}>
                <Text style={styles.text}>Create New Room</Text>
                <TextInput style={styles.textInput} placeholder="Enter new room name" value={this.state.newRoomName} onChangeText={
                    (text) => this.setState({newRoomName: text})
                }/>
                <View style={{ alignItems: "center" }}>
                    <View style={styles.buttonCon}>
                        <TouchableOpacity onPress={this.onCreateButtonPress}>
                            <View style={styles.button}>
                                <Text style={styles.buttonText}>Create</Text>
                            </View>
                        </TouchableOpacity>
                    </View>
                    <View style={styles.buttonCon}>
                        <TouchableOpacity onPress={this.onCloseButtonPress}>
                            <View style={styles.button}>
                                <Text style={styles.buttonText}>Close</Text>
                            </View>
                        </TouchableOpacity>
                    </View>
                </View>
            </Modal>
        );
    }
}

const styles = StyleSheet.create({
    modalContainer: {
        justifyContent: 'center',
        borderRadius: 30,
        shadowRadius: 10,
        width: screen.width - 80,
        height: 280,
        borderWidth: 2,
        borderColor: '#8ecfe8'
    },
    textInput: {
        height: 40,
        borderBottomColor: 'gray',
        marginLeft: 30,
        marginRight: 30,
        marginTop: 20,
        marginBottom: 10,
        borderBottomWidth: 1
    },
    text: {
        fontSize: 16,
        fontWeight: 'bold',
        textAlign: 'center',
        marginTop: 40
    },
    button: {
        padding: 8,
        marginTop: 3,
        marginBottom: 3,
        height: 40,
        width: 150,
        borderRadius: 6,
        backgroundColor: '#8ecfe8',
        alignItems: 'center'
    },
    buttonCon: {
        width: 150,
    },
    buttonText: {
        color: 'white',
    }
})
