import {StyleSheet, Text, View, Button, Image, TouchableOpacity, Platform, StatusBar} from "react-native";
import {TextInput} from "react-native-paper";
import React from "react";
import Fire from "./Fire";

export default class SignUp extends React.Component {
  state = {
      email: "",
      password: "",
      confirmPassword: "",
      errorMessage: null
  };

  handleSignUp = () => {
      if (this.state.password !== this.state.confirmPassword){
          this.setState({ errorMessage: "The two passwords do not match!" });
      }
      else{
          Fire.auth
              .createUserWithEmailAndPassword(this.state.email, this.state.password)
              .then(() => this.props.navigation.navigate("RoomList"))
              .catch(error => this.setState({ errorMessage: error.message }));
      }
  };

  render() {
      return (
          <View style={styles.androidSafeZone}>
              <View style={[styles.container50, styles.stickToBottom]}>
                  <View style={styles.imageIconContainer}>
                      <Image style={styles.imageIcon} source={require("../assets/AppIcon.png")}/>
                  </View>
                  <View style={styles.welcomeText}>
                      <Text style={styles.welcomeTextContent}>Register to Playlist App!</Text>
                  </View>
                  <View style={{ justifyContent: 'center', alignItems: 'center' }}>
                      {this.state.errorMessage && (
                          <Text style={{ color: "red" }}>{this.state.errorMessage}</Text>   // <-------------------------------------------
                      )}
                  </View>

              </View>
              <View style={[styles.container50, styles.centerContent]}>
                  <View>
                      <TextInput style={styles.textInput} label="Email Address" onChangeText={email => this.setState({ email })} value={this.state.email}/>
                      <TextInput style={styles.textInput} label="Password" onChangeText={password => this.setState({ password })} value={this.state.password}/>
                      <TextInput style={styles.textInput} label="Confirm Password" onChangeText={confirmPassword => this.setState({ confirmPassword })} value={this.state.confirmPassword}/>
                  </View>
                  <TouchableOpacity style={styles.appButtonContainer} onPress={this.handleSignUp}>
                      <Text style={styles.appButtonText}>Register</Text>
                  </TouchableOpacity>
                  <View style={styles.centerContent2}>
                      <Text onPress={() => this.props.navigation.navigate("Login")} >Already have an account? Login</Text>
                  </View>
              </View>
          </View>
      );
  }
}

